import { html, TemplateResult } from "lit-html";
import { repeat } from "lit-html/directives/repeat";
import { until } from "lit-html/directives/until";
import { ITemplateRetriever } from "../../genericdetailviewmanager/interfaces/itemplateretriever";
import { CheckboxService } from "../discontinued/services/checkboxservice";
import { DateService } from "../discontinued/services/dateservice";
import { IconService } from "../discontinued/services/iconservice";
import { LinkService } from "../discontinued/services/linkservice";
import { ListService } from "../discontinued/services/listservice";
import { ObjectService } from "../discontinued/services/objectservice";
import { TextService } from "../discontinued/services/textservice";
import { DataFormatter } from "../formatters/dataformatter";
import { TableFormatter } from "../formatters/tableformatter";
import { DataRetriever } from "../helpers/dataretriever";
import { Field, Section } from "../models/templatemodel";
import { FieldWriter } from "../writers/fieldwriter";

export class TemplateEngine {
    private subViewState: any = null;
    private templateRetriever: ITemplateRetriever = null
    private host: any = null;
    private dateService: DateService = null;
    private textService: TextService = null
    private iconService: IconService = null;
    private checkboxService: CheckboxService = null;

    public constructor(templateRetriever: ITemplateRetriever, host: any) {
        this.templateRetriever = templateRetriever;
        this.host = host;

        let dataHelper = new DataRetriever();
        this.dateService = new DateService(dataHelper);
        this.textService = new TextService(dataHelper);
        this.iconService = new IconService(dataHelper);
        this.checkboxService = new CheckboxService(dataHelper);
    }

    private SectionWriters = {
        "1": (data, section) => this.renderSectionOld(data, section),
        "2": (data, section) => this.renderSectionOld(data, section),
        "3": (data, section) => this.renderSectionOld(data, section),
        "4": (data, section) => this.renderListWithSubViewSection(data, section),
        "6": (data, section) => this.renderNonClickableList(data, section),
        "8": (data, section) => this.renderExternalData(data, section),
        "Standard": (data, section) => this.renderStandard(data, section),
        "ClickableList": (data, section) => this.renderClickableList(data, section),
        "Table": (data, section) => TableFormatter.renderTable(data, section),
        "Tab": (data, section) => this.renderTabList(data, section),
        "ExternalData": (data, section) => this.renderExternalDataNew(data, section)
    }

    private FieldWriters = {
        "LabelWithData": (data, field) => FieldWriter.getLabelWithData(data, field),
        "Multiline": (data, field) => FieldWriter.getMultiline(data, field),
        "CheckBox": (data, field) => FieldWriter.getCheckbox(data, field),
        "Empty": () => FieldWriter.getEmptyField(),
        "Line": () => FieldWriter.getLineField()
    }

    private FieldWriter_TOBEDISCONTINUED = {
        "1": (data, field) => this.textService.getTextOneLine(data, field),
        "2": (data, field) => this.textService.getTextTwoLines(data, field),
        "3": (data, field) => LinkService.getLinktoHTML(data, field),
        "4": (data, field) => this.dateService.getDate(data, field),
        "5": (data, field) => this.textService.getSplitStringOnFirstOccurenceSelectSpecificPart(data, field, 0),
        "6": (data, field) => this.textService.getDataValueToString(data, field),
        "7": (data, field) => this.textService.getFieldValueToString(data, field),
        "8": (data, field) => this.textService.getTextNoLabel(data, field),
        "9": (data, field) => this.dateService.getDateNoLabel(data, field),
        "10": (data, field) => this.textService.getSplitStringOnFirstOccurenceSelectSpecificPart(data, field, 1),
        "11": (data, field) => ObjectService.getObjectValuesWithDevider(data, field),
        "12": (data, field) => this.textService.getArrayValues(data, field),
        "13": (data, field) => this.checkboxService.getCheckboxWithLabel(data, field),
        "14": (data, field) => this.iconService.getIconWithLabel(data, field),
        "15": (data, field) => this.iconService.getLineSeperator(data, field),
        "16": (data, field) => this.textService.getValueWithDenomination(data, field),
        "17": (data, field) => this.iconService.getEmptyLine(data, field),
        "18": (data, field) => this.textService.staticText(data, field),
        "19": (data, field) => this.textService.formattedNumbers(data, field),
        "20": (data, field) => this.textService.customBooleanValues(data, field),
        "21": (data, field) => this.textService.getConcattedValues(data, field),
        "22": (data, field) => this.textService.getStringReplaceWithData(data, field),
        "23": (data, field) => this.textService.getValueInComplexObject(data, field)
    }

    //-------------------------------------------------------------------------
    // 
    //  Render Section...
    //
    //-------------------------------------------------------------------------

    public renderData(data: any, sections: Section[]): TemplateResult {
        return html` ${repeat(sections, s => s.Id, s => {
            try {
                return this.renderTemplateSections(data, s)
            }
            catch (e) {
                return html`<div class="exception">${e}</div>`
            }
        }
        )}`;
    }

    private renderTemplateSections(data: any, section: Section): TemplateResult {
        try {
            return html`<div id="_${section.Id}" class="SectionType_${section.Type}"> ${this.SectionWriters[section.Type](data, section)} </div>`;
        }
        catch (e) {
            return html`<div class="exception">${e}</div>`
        }
    }

    private renderTemplateFieldsNew(data: any, fields: Field[]): TemplateResult {
        return html`${repeat(fields, (f: any) => f.Id, f => {
            try {
                return this.FieldWriters[f.Type](DataFormatter.getFormattedValue(data, f), f)

            } catch (e) {
                return html`<div class="exception">${e}</div>`
            }
        }
        )}`;
    }

    //-------------------------------------------------------------------------
    // 
    //  Private methods Section
    //
    //-------------------------------------------------------------------------

    private renderStandard(data: any, section: any) {
        try {
            return html`${this.renderTemplateFieldsNew(data, section.Fields)}`

        } catch (e) {
            return html`<div class="exception">${e}</div>`
        }
    }

    private renderClickableList(data: any, section: Section): TemplateResult {
        if (!data.__viewState) {
            data.__viewState = {};
        }
        this.subViewState = data.__viewState[section.Id] = data.__viewState[section.Id] || {};
        let list = DataFormatter.retreiveData(data, section.ValueDescriptor.Path);
        let listItemIdentifier = section.Options.ListItemIdentifier;
        if (list.length == 0) return html``;

        //Format list
        let formattedList = list;
        section.Formatters.forEach(f => {
            formattedList = TableFormatter.appylFormatters(formattedList, f)

        });

        try {
            return html`
                    <div class="list-label">${section.Options.Title} (${formattedList.length})</div>
                    <div id="container-list-items" class="container-list-items">
                        <ul>
                            ${repeat(formattedList, i => i[listItemIdentifier], i => this.renderListItem(i, section))}
                        </ul>
                    </div>
                    ${this.renderSelectedListItem(list, section)}`;
        }
        catch (e) {
            return html`<div class="exception">${e}</div>`
        }
    }

    private getItemLabel(data: any, section: Section): TemplateResult {
        return html`${repeat(section.Fields, f => {
            if (f.ValueDescriptor.Type == 'static') return html`${f.ValueDescriptor.Path}`;
            return DataFormatter.retreiveData(data, f.ValueDescriptor.Path);
        })}`;
    }

    private renderListItem(item: any, section: Section): TemplateResult {

        return html`
            <li>
                <div class='list-item' @click=${() => { this.listItemOnClick(item[section.Options.ListItemIdentifier]) }}>
                    <span class='label'>${this.getItemLabel(item, section)}</span>
                    <span class='container-arrow'>
                        <svg class='more arrow' height="9.471" viewBox="0 0 5.848 9.471">
                            <path d="M8.59,14.358,12.2,10.735,8.59,7.113,9.7,6l4.735,4.735L9.7,15.47Z" transform="translate(-8.59 -6)" />
                        </svg>
                    </span>
                </div>
            </li>`;
    }

    private renderSelectedListItem(list: any, section: Section): TemplateResult {

        return html`${this.subViewState.selected ?
            until(this.renderTemplateForListItem(list.find(e => e[section.Options.ListItemIdentifier] == this.subViewState.selected), section.Options.TemplateId), html``) : html``}`;
    }

    private async renderTemplateForListItem(data: any, templateId: string): Promise<TemplateResult> {

        let template = await this.templateRetriever.getTemplateFromTemplateId(templateId);

        let sections = template.Template.Content as unknown as Section[];

        return html`
                    <div class="slide-in" id="slider">
                        <div class="detail-view ${this.host.viewClass}">
                            <div class="container-button">${this.renderBackButton()}</div>
                            <div> ${(this.renderData(data, sections))}</div>
                        </div>
                    </div>`;
    }

    private async renderTemplateForTab(data: any, templateId: string): Promise<TemplateResult> {
        let template = await this.templateRetriever.getTemplateFromTemplateId(templateId);
        let sections = template.Template.Content as unknown as Section[];
        return html`<div>${this.renderData(data, sections)}</div>`
    }

    private renderTabList(data: any, section: Section): TemplateResult {


        let tabcontent = DataFormatter.retreiveData(data, section.ValueDescriptor.Path);
        if (tabcontent.length == 0) return html``;

        let tabState = {
            currentIndex: 0, tabOnClick: (index, element) => {

                [...element.parentElement.children].forEach(c => c.classList.remove('selectedTab'));
                element.classList.add('selectedTab');
                tabState.currentIndex = index;

                let tabcontent = [...element.closest('div').children];
                tabcontent.forEach(c => c.classList.remove('selectedContent'));
                tabcontent[index + 1].classList.add('selectedContent');
            }
        };

        return html`<div class='tabstrip'>
                    <ul class="tabheader" id='tabheader'>
                        ${repeat(tabcontent, (t, i) =>
            html` 
                                <li class="tab-button ${i === tabState.currentIndex ? 'selectedTab' : ''}"  @click=${(e) => { tabState.tabOnClick(i, e.target) }}>
                                    ${this.getItemLabel(t, section)}
                                </li>`
        )}
                    </ul>
            ${repeat(tabcontent, (t, i) =>
            html`
                   <div id='tabcontent' class='tabcontent ${i === tabState.currentIndex ? 'selectedContent' : ''}'>  ${until(this.renderTemplateForTab(t, section.Options.TemplateId), html`..Loading`)}</div>`
        )}
                </div>`;
    }

    private renderExternalDataNew(data: any, section: any): TemplateResult {

        let resolveFunction;
        const promise = new Promise<void>((resolve, reject) => {
            resolveFunction = resolve;
        });

        let callback = (callbackData) => {
            try {
                callbackData ? resolveFunction(html`<span class="popupexternaldata" @click=${(event) => this.host.openPopUpExternalData(callbackData, section.ValueDescription.Path, event)}>${this.renderTemplateFieldsNew(callbackData, section.Fields)}</span>`)
                    : null;
            } catch (error) {
                console.log(error.message)
            }
        };
        this.host.loadExternalData(data, section.Options.datasource, callback);

        //Render plugin data
        return html`${until(promise, html``)}`
    }

    private renderBackButton(): TemplateResult {
        return html`
                <div id="toggle" class='back-button' @click=${(e) => this.toggleSubView()}>
                    <div>                
                        <svg class="arrow" viewBox="0 0 16 8">
                            <path d="M7.99,11H20v2H7.99v3L4,12,7.99,8Z" transform="translate(-4 -8)"/>
                        </svg>
                        Back
                    </div>
                </div>`;
    }

    private toggleSubView(): void {
        let slider = this.host.shadowRoot.getElementById('slider');
        if (slider) {
            let isOpen = slider.classList.contains('slide-in');
            slider.setAttribute('class', isOpen ? 'slide-out' : 'slide-in');
        }
    }

    private listItemOnClick(item: any) {
        this.subViewState.selected = item;
        this.host.requestUpdate();
        this.toggleSubView();
    }

    //-------------------------------------------------------------------------
    // 
    //  Private Methods Section...  DISCONTINUED methods
    //
    //-------------------------------------------------------------------------
    private renderTemplateFields(data: any, fields: Field[]): TemplateResult {
        return html`${repeat(fields, (f: any) => f.Id, f => this.FieldWriter_TOBEDISCONTINUED[f.Type](data, f))}`;
    }

    private renderSectionOld(data: any, section: Section): TemplateResult {
        return html`${this.renderTemplateFields(data, section.Fields)}`
    }

    private renderListWithSubViewSection(data: any, section: Section): TemplateResult {
        if (!data.__viewState) {
            data.__viewState = {};
        }
        this.subViewState = data.__viewState[section.Id] = data.__viewState[section.Id] || {};
        let listName = section.Options.Source;
        let list = data[listName];
        let listItemIdentifier = section.Options.ListItemIdentifier;
        if (list.length == 0) return html``;

        let sortedlist = ListService.getSortedList(list, section);

        return html`
                    <div class="list-label">${section.Options.Title} (${sortedlist.length})</div>
                    <div id="container-list-items" class="container-list-items">
                        <ul>
                            ${repeat(sortedlist, i => i[listItemIdentifier], i => this.renderClickableListItem(i, section))}
                        </ul>
                    </div>
                    ${this.renderSubDetailView(list, section)}`;
    }

    private getListItemLabel(data: any, section: Section): string {
        let labelString = '';
        section.Fields.forEach(f => {
            labelString += this.FieldWriter_TOBEDISCONTINUED[f.Type](data, f)
        });
        return labelString;
    }

    public renderExternalData(data: any, section: any): TemplateResult {

        let resolveFunction;
        const promise = new Promise<void>((resolve, reject) => {
            resolveFunction = resolve;
        });

        let callback = (callbackData) => {
            try {
                callbackData ? resolveFunction(html`<span class="popupexternaldata" @click=${(event) => this.host.openPopUpExternalData(callbackData, section.Options.datasource, event)}>${this.renderTemplateFields(callbackData, section.Fields)}</span>`)
                    : null;
            } catch (error) {
                console.log(error.message)
            }
        };
        this.host.loadExternalData(data, section.Options.datasource, callback);

        //Render plugin data
        return html`${until(promise, html``)}`
    }

    private renderClickableListItem(item: any, section: Section): TemplateResult {

        return html`
            <li>
                <div class='list-item' @click=${() => { this.listItemOnClick(item[section.Options.ListItemIdentifier]) }}>
                    <span class='label'>${this.getListItemLabel(item, section)}</span>
                    <span class='container-arrow'>
                        <svg class='more arrow' height="9.471" viewBox="0 0 5.848 9.471">
                            <path d="M8.59,14.358,12.2,10.735,8.59,7.113,9.7,6l4.735,4.735L9.7,15.47Z" transform="translate(-8.59 -6)" />
                        </svg>
                    </span>
                </div>
            </li>`;
    }

    private renderNonClickableListItem(item: any, fields: Field[]): TemplateResult {

        return html`
                    ${repeat(fields, f => f.Id, f => html`<div class="FieldType_${f.Type}"> ${this.FieldWriter_TOBEDISCONTINUED[f.Type](item, f)}</div>`)}`;
    }

    private renderSubDetailView(list: any, section: Section): TemplateResult {

        return html`${this.subViewState.selected ?
            until(this.renderSliderDetailView(list.find(e => e[section.Options.ListItemIdentifier] == this.subViewState.selected), section.Options.TemplateId), html``) : html``}`;
    }

    private async renderSliderDetailView(data: any, templateId: string): Promise<TemplateResult> {

        let template = await this.templateRetriever.getTemplateFromTemplateId(templateId);

        let sections = template.Template.Content as unknown as Section[];

        return html`
                    <div class="slide-in" id="slider">
                        <div class="detail-view ${this.host.viewClass}">
                            <div class="container-button">${this.renderBackButton()}</div>
                            <div> ${(this.renderData(data, sections))}</div>
                        </div>
                    </div>`;
    }

    private renderNonClickableList(data: any, section: Section): TemplateResult {
        let listName = section.Options.Source;
        let list = data[listName];
        let listItemIdentifier = section.Options.ListItemIdentifier;
        if (list.length == 0) return html``;

        return html`
                    <div>
                            ${repeat(list, i => i[listItemIdentifier], i => this.renderNonClickableListItem(i, section.Fields))}
                    </div>`;
    }
}