import { html, TemplateResult } from "lit-html";
import { DataValidator } from "../helpers/datavalidator";
import { Field } from "../models/templatemodel";

export class FieldWriter {

    static getLabelWithData(data: any, field: Field): TemplateResult {

        if (data) {
            return html`<div><span class="label ${field?.Options?.styleClass}">${field.Label}&nbsp;</span><span class="value">${data}</span></div>`;
        }

        if (field?.Options?.hideLabelIfDataIsEmpty) {
            return html``;
        }

        return html` <div> <span class="label" > ${field.Label}&nbsp; </span></div>`;
    }

    static getEmptyField(): TemplateResult {
        return html`<br>`;
    }

    static getLineField(): TemplateResult {
        return html`<hr>`;
    }

    static getMultiline(data: any, field: Field): TemplateResult {

        if (data) {
            let multiline = field.Options.Multiline;

            // check if multiline is not configuredd in SysAdmin even though the data require multiline
            if (DataValidator.isEmpty(multiline)) {

                //default is multiline 5 lines
                multiline = 5;
            }
            return html`<div><span class="label ${field?.Options?.styleClass}" style="display:block">${field.Label}</span><div class="textContainer multiline" style = "max-height:${multiline}em" id = "multiline" > ${data} </div></div>`;
        }

        if (field?.Options?.hideLabelIfDataIsEmpty) {
            return html``;
        }

        return html` <div> <span class="label"> ${field.Label}</span></div>`;
    }

    static getCheckbox(data: any, field: Field): TemplateResult {

        if (data === false || data === true) {
            let isTrue = data === true;

            let checkbox = isTrue ? html`<input type='checkbox' checked />` : html`<input type='checkbox' />`;
            return html`<div ><div style="display:inline" class="checkboxContainer" > ${checkbox}<span class="checkmark" > </span> </div ><span class="label ${field?.Options?.styleClass}" style="display:inline">${field.Label}</span></div>`;
        }

        if (field?.Options?.hideLabelIfDataIsEmpty) {
            return html``;
        }

        return html` <div > </span> <div class="checkboxContainer" > <input type='checkbox' /> <span class="checkmark" > </span></div> <span class="label" > ${field.Label} </div>`;
    }
}