import { html, TemplateResult } from "lit-html";
import { DateFormatter } from "../../../shared/dateformat";
import { DataRetriever } from "../../helpers/dataretriever";
import { Field } from "../../models/templatemodel";

export class DateService {
    private _dataHelper: DataRetriever;
    private _dateFormatter: DateFormatter = null;

    public constructor(dataHelper: DataRetriever) {
        this._dataHelper = dataHelper;
        this._dateFormatter = new DateFormatter();
    }

    public getDate(data: any, field: Field): TemplateResult {
        let date = data[field.Value];
        if (field.Value.includes(".")) {
            let objectToSplit = field.Value.split('.');
            let object = objectToSplit[0];
            let value = objectToSplit[1];
            let dataObject = data[object];
            date = dataObject[value];
        }
        if (date === undefined || date === "" || date === null) {
            try {
                let text = html`${this._dataHelper.toggleLabel(field)}
                    <span class="${field.Format}"></span>`;
                return this._dataHelper.outputValidator(data, field, text);
            } catch (error) {
                console.log(error.message);
            }
        }

        try {
            let formatedDate = this._dateFormatter.dateFormat(new Date(date), field.Format);
            let text = html`${this._dataHelper.toggleLabel(field)}
                    <span class="${field.Format}">${formatedDate}</span>`;
            return this._dataHelper.outputValidator(data, field, text);
        } catch (error) {
            console.log(`Error in field ${field.Type}:`, error.message);
            if (error instanceof RangeError) {
                console.log("Possible solutions: Value does not exist in dataset, or typos in template properties");
            }
        }
    }


    public getDateNoLabel(data: any, field: Field): TemplateResult {
        let date = this._dateFormatter.dateFormat(new Date(data[field.Value]), field.Format);
        let text = html`<span class="${field.Format}">${date}</span>`;
        return html`<span id="${field.Id}" class="FieldType_${field.Type}">${text}</span>`;
    }
}