import { PeriodTypes } from "./Period/PeriodTypes";

class Week {
    WeekNo: number;
    Mon: Date;
    Tue: Date;
    Wed: Date;
    Thu: Date;
    Fri: Date;
    Sat: Date;
    Sun: Date;
}

export class dateUtil {
    private _date: Date;
    private _curWeek: Week = null;

    constructor(date?: Date, config?: DateUtilConfig) {
        this._date = date || new Date();
    }

    setDate(d) {
        this._date = d;
        this._curWeek = null;
    }

    today() {
        return this._date;
    }

    addDays(numberOfDays) {
        let newDay = this._date.getDate() + numberOfDays;
        return new Date(this._date.getFullYear(), this._date.getMonth(), newDay);
    }

    firstPastWeekday(dayOfweek) {
        var temp = new Date(this._date.getFullYear(), this._date.getMonth(), this._date.getDate());
        while (temp.getDay() != dayOfweek) { temp.setDate(temp.getDate() - 1) }

        return temp;
    }


    public get currentWeek(): Week {

        if (this._curWeek) return this._curWeek;

        var week = [];
        var mon = new dateUtil(this.firstPastWeekday(1));

        for (var x = 0; x - 7; x++) {
            week[x] = mon.addDays(x);
        }


        let ret = new Week();
        ret.WeekNo = calculateWeekNo(this._date);
        ret.Mon = week[0];
        ret.Tue = week[1];
        ret.Wed = week[2];
        ret.Thu = week[3];
        ret.Fri = week[4];
        ret.Sat = week[5];
        ret.Sun = week[6];
        return ret;
    }


}

//1. dow = monday
//1. woy = first 4 days
export function calculateWeekNo(d: Date) {
    let dayMap = [6, 0, 1, 2, 3, 4, 5];
    let year = d.getFullYear();
    let month = d.getMonth();
    let day = d.getDate();
    let dow = dayMap[d.getDay()];

    let mondayInWeek = Date.UTC(year, month, (day - dow));
        
    let jan1st = new Date(Date.UTC(year, 0, 1));
    let jan1stDow = dayMap[jan1st.getDay()];

    let firstMondayOfYear = jan1stDow < 4 ? Date.UTC(year - 1, 11, 32 - jan1stDow) : Date.UTC(year, 0, 7 - jan1stDow);
        

    let diff = mondayInWeek - firstMondayOfYear;

    return Math.floor(1 + (diff / 604800000));

}



export function periodNoCalculator(date: Date) {
    var res;
    var firstDateOfYear = Date.UTC(date.getFullYear(), 0, 1);
    var thisDay = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours())
    const msInDay = 1000 * 60 * 60 * 24;
    var y = date.getFullYear();

    let d = Math.floor(((thisDay - firstDateOfYear) / msInDay)) +1;
    // d = d || 1; //0 is edge case

    let dFormat = ("" + d).padStart(3, "0");

    var h = `${date.getHours()}`.padStart(2, "0");

    //res = parseInt("" + y + d + h);

    res = `${y}${dFormat}${h}`;
    return parseInt(res);
}

export class DateUtilConfig {
    firstDayOfWeek: number;
    firstWeekOfYear: string;

    constructor() {
        this.firstDayOfWeek = 1; //monday
        this.firstWeekOfYear = 'firstFourDays';
    }
}

export function minDate(...args: Date[]): Date | null {
    if (args.length > 0) {
        let minD = args[0];
        for (let i = 1; i < args.length; i++) {
            if (args[i] < minD) minD = args[i];
        }
        return minD;
    }
    return null;
}

export function maxDate(...args: Date[]): Date | null {
    if (args.length > 0) {
        let maxD = args[0];
        for (let i = 1; i < args.length; i++) {
            if (args[i] > maxD) maxD = args[i];
        }
        return maxD;    }
    return null;
}

 export function milliSecondsInPeriodType(periodType: PeriodTypes): number {

    switch (periodType) {
        default:
        case PeriodTypes.Day: return 86400000;
        case PeriodTypes.Hour: return 3600000;
        case PeriodTypes.Week: return 604800000;
     }
}

export function getMilliSecondsInTheMonthDateIsIn(date: Date): number {
    return new Date(date.getFullYear(), date.getUTCMonth() + 1, 0).getDate() * 24 * 60 * 60 * 1000;
}

export function dateToLocalString(date: Date) {
    return `${date.getFullYear()}-${padNumber(date.getMonth() + 1)}-${padNumber(date.getDate())} ${padNumber(date.getHours())}:${padNumber(date.getMinutes())}:${padNumber(date.getSeconds())}`;
}

function padNumber(number: number): string {
    return number.toString().padStart(2, "0");
}
