import timeoutPromise from './requestTimeoutUtil';

export function toQueryParams(o) {

    if (!o) return '';

    var ret = Object.entries(o).map(([key, val]) => {
        return `${encodeURIComponent(key)}=${encodeURIComponent(val instanceof Date ? formatdate(val) : val ? val.toString() : '')}`
    }).join('&');
    return ret;
}

export async function QueryServer<T>(query: any , name: string =  null) : Promise<T> {
        
    var qName =  name ? name : query.constructor.name;

    document.dispatchEvent(new CustomEvent('ServerQueryStarted'));

    try {
        const promise = fetch(`/q/${qName}`, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json'
            },
            redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(query, handelSerializer) // body data type must match "Content-Type" header
        });

        let res = await timeoutPromise(promise);
        
        let req = await res.json();

        //document.dispatchEvent(new CustomEvent('ServerQueryEnded', { Data: {} }))
        return <T>req;

    } catch (e) {
        document.dispatchEvent(new CustomEvent('ServerQueryFailed'));
        console.error(e);
        //document.dispatchEvent(new CustomEvent('ServerQueryFailed', { Data: {} }))
        throw e;
    }  finally {
        document.dispatchEvent(new CustomEvent('ServerQueryEnded'));
    }
}

export async function QueryServer2<T>(query: any, name: string = null): Promise<T> {
    //
    // TODO! Use it (rename + verify & fix the code that use the QueryServer function) or remove it.
    //       It fixes the problem with datetime data type in json result.
    //       Parses ISO-8601 compliant date-time strings and returns Date objects.
    // 
    var qName = name ? name : query.constructor.name;

    document.dispatchEvent(new CustomEvent('ServerQueryStarted'));

    try {
        const promise = fetch(`/q/${qName}`, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json'
            },
            redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(query, handelSerializer) // body data type must match "Content-Type" header
        });

        let res = await timeoutPromise(promise);

        let text = await res.text();

        let dateRegExp = new RegExp(/^(-?(?:[1-9][0-9]*)?[0-9]{4})-(1[0-2]|0[1-9])-(3[01]|0[1-9]|[12][0-9])T(2[0-3]|[01][0-9]):([0-5][0-9]):([0-5][0-9])(\.[0-9]+)?(Z|[+-](?:2[0-3]|[01][0-9]):[0-5][0-9])?$/);

        return <T>JSON.parse(text, (key, value) => {
            if (typeof value === 'string') {
                // Parse ISO-8601 date strings
                let result = dateRegExp.exec(value);
                if (result) {
                    return new Date(value);
                }
            }
            return value;
        });
    } catch (e) {
        document.dispatchEvent(new CustomEvent('ServerQueryFailed'));
        console.error(e);
        throw e;
    } finally {
        document.dispatchEvent(new CustomEvent('ServerQueryEnded'));
    }
}

export async function GET<T>(path: string, params: any = null): Promise<T> {


    document.dispatchEvent(new CustomEvent('ServerQueryStarted'));
    try {
        const promise = fetch(`${path}?${toQueryParams(params)}`, {
            method: 'GET', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json'
            },
            redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *client
        });

        let res = await timeoutPromise(promise);

        let req = await res.json();
        
        return <T>req;

    } catch (e) {
        document.dispatchEvent(new CustomEvent('ServerQueryFailed'));
        console.error(e);
        throw e;
    } finally {
        document.dispatchEvent(new CustomEvent('ServerQueryEnded'));
    }

}

export async function ExecuteOnServer(query: any, name: string = null): Promise<void> {

    var qName = name ? name : query.constructor.name;
    document.dispatchEvent(new CustomEvent('ServerQueryStarted'));

    try {
        const promise = fetch(`/cmd/${qName}`, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json'
            },
            redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(query, handelSerializer) // body data type must match "Content-Type" header
        });

        let res = await timeoutPromise(promise);

        if (!res.ok) throw await res.text();

        return;

    } catch (e) {
        document.dispatchEvent(new CustomEvent('ServerQueryFailed'));
        console.error(e);
        throw e;
    } finally {
        document.dispatchEvent(new CustomEvent('ServerQueryEnded'));
    }

}

export async function ExecuteOnServerWithResult<T>(command: any, name: string = null): Promise<T> {

    var qName = name ? name : command.constructor.name;
    document.dispatchEvent(new CustomEvent('ServerQueryStarted'));

    try {
        const promise = fetch(`/cmd/${qName}`, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json'
            },
            redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(command, handelSerializer) // body data type must match "Content-Type" header
        });

        let res = await timeoutPromise(promise);

        if (!res.ok) throw await res.text();

        let req = await res.json();

        return <T>req;

    } catch (e) {
        document.dispatchEvent(new CustomEvent('ServerQueryFailed'));
        console.error(e);
        throw e;
    } finally {
        document.dispatchEvent(new CustomEvent('ServerQueryEnded'));
    }
}

function handelSerializer(key, value) {
    
    if (key) {
        return this[key] instanceof Date ? datetostring(this[key]) : value;
    } 
    return value;
       
}

function datetostring(val: Date): string {
    
    let pad = (num) => num.toString().padStart(2, "0");

    let ret = `${val.getFullYear()}-${pad(val.getMonth() + 1)}-${pad(val.getDate())}T${pad(val.getHours())}:${pad(val.getMinutes())}:${pad(val.getSeconds())}`

    return ret;    
}

function formatdate(val: Date): string {
    let res = JSON.stringify(val).replace(/"/ig, '');
    return res;
}
