import { QueryServer2 } from "../../shared/toQueryParams";
import { ITemplateRetriever } from "../interfaces/itemplateretriever";

export class TemplateRetriever implements ITemplateRetriever {

    public templates = new Map<String, Promise<server.TemplateDataDTO>>();

    public async getTemplateFromTemplateId(templateId: string): Promise<server.TemplateDataDTO> {

        if (!this.templates.has(templateId)) {
            this.templates.set(templateId, QueryServer2<server.TemplateDataDTO>(new GetTemplateDataQuery({
                TemplateId: templateId,
            })))

        }
        return await this.templates.get(templateId);
    }

    public async getTemplateId(scopeId: string, dataStreamid: string, dataItemType: string): Promise<server.TemplateIdDTO> {
        return await QueryServer2<server.TemplateIdDTO>(new GetDetailViewTemplateIdQuery({
            ScopeId: scopeId,
            DateStreamId: dataStreamid,
            DataItemType: dataItemType
        }));
    }
}
class GetTemplateDataQuery implements server.GetTemplateDataQuery {
    TemplateId: any;

    constructor(init?: Partial<GetTemplateDataQuery>) {
        Object.assign(this, init);
    }
}
class GetDetailViewTemplateIdQuery implements server.GetDetailViewTemplateIdQuery {
    ScopeId: any;
    DateStreamId: any;
    DataItemType: any;

    constructor(init?: Partial<GetDetailViewTemplateIdQuery>) {
        Object.assign(this, init);
    }
}

