import { html, TemplateResult } from "lit-html";
import { repeat } from "lit-html/directives/repeat";
import { DataValidator } from "../helpers/datavalidator";
import { Section } from "../models/templatemodel";
import { DataFormatter } from "./dataformatter";

export class TableFormatter {


    static tableWriters = {
        "sumCol": (table, fields, text, colNum) => TableFormatter.sumCol(table, fields, text, colNum)
    }

    static renderTable(data: any, section: Section): TemplateResult {

        // Retrieve list with data
        let list = DataFormatter.retreiveData(data, section.ValueDescriptor.Path);

        // Resulting table
        let resultTable = new Table();

        // Header
        resultTable.header = [];
        resultTable.header.push([...section.Fields.map(f => f.Label)]);

        // Body
        for (let i = 0; i < list.length; i++) {
            let row = [];
            let d = list[i];
            section.Fields.forEach(f => {
                row.push(DataFormatter.getFormattedValue(d, f));
            });
            resultTable.body.push(row);
        }

        // Footer
        section.Formatters.forEach(f => {
            resultTable = TableFormatter.appylFormatters(resultTable, f)

        });

        return html` <table>
                        <thead>
                             ${repeat(resultTable.header, r => html`<tr>${repeat(<any>r, c => html`<th>${c}</th>`)} </tr>`)}
                        </thead>
                        <tbody id="table-items">    
                             ${repeat(resultTable.body, r => html`<tr>${repeat(<any>r, c => html`<td>${c}</td>`)} </tr>`)}     
                        </tbody>
                        ${resultTable.footer.length ? html`
                        <tfoot>
                             ${repeat(resultTable.footer, r => html`<tr>${repeat(<any>r, c => html`<td>${c}</td>`)} </tr>`)}
                        </tfoot>  `: html``}
                               
                    </table>`
    }

    static appylFormatters(data: Table, formatter: string): any {
        let temp = formatter.match(/(.*)\((.*)\)/);
        let params = window.eval('[' + temp[2] + ']');
        let funcName = temp[1];
        if (TableFormatter.tableWriters[funcName]) {
            return TableFormatter.tableWriters[funcName].apply(null, [data, ...params]);
        }
        return data;
    }

    static sumCol(table: Table, fields: Array<number>, text: string, colNum: number): Table {

        let tempTable = new Table();
        let offset = 0;
        if (colNum == -1) {
            for (let i in table) {
                for (let j of table[i]) {
                    tempTable[i].push([null, ...j]);
                }
            }
            offset = 1;
        } else {
            tempTable.header = [...table.header];
            tempTable.body = [...table.body];
            tempTable.footer = [...table.footer];
        }

        let result = [];
        fields.forEach(f => {
            let sum = 0;
            try {
                sum = tempTable.body.reduce((agg, curr) => {

                    let tall = curr[f + offset];
                    let number = DataValidator.isNumber(tall);
                    return agg += number ? parseFloat(tall) : 0;
                }, 0)
                result[f + offset] = sum;
            } catch (e) {
                result[f + offset] = e;
            }
        });

        for (var i = 0, n = tempTable.body[0].length; i < n; ++i) {
            if (result[i] === undefined) {       // explicit check for missing sparse value
                result[i] = '';
            }
        }
        result[offset + colNum] = text;
        tempTable.footer.push(result);
        return tempTable;
    }

}
class Table {
    public header = [];
    public body = [];
    public footer = [];
}
