export class NumberFormatter {


    static numberfunctions = {
        setDenomination: (value, denomination) => NumberFormatter.setDenomination(value, denomination),
        setDecimales: (value, decimales) => NumberFormatter.setDecimales(value, decimales),
        join: (array, separator) => NumberFormatter.join(array, separator)

    }

    static setDecimales(value: any, decimales: string): string {
        if (value instanceof Array) {
            value.map(e => NumberFormatter.roundNumberAndSetDecimales(e, decimales));
        }
        return NumberFormatter.roundNumberAndSetDecimales(value, decimales);
    }

    static roundNumberAndSetDecimales(value: any, decimales: string): string {
        let textNumber = parseFloat(value);
        let roundedNumber = Math.pow(10, parseFloat(decimales));
        let number = Math.round(textNumber * roundedNumber) / roundedNumber;
        return number.toString();
    }

    static setDenomination(value: any, denomination: string): string {
        let string = value + ' ';
        return string.concat(denomination);
    }

    static join(array: any, separator: string): string {
        return Array.prototype.join.apply(array, separator);
    }

}

