import { jsonQuery } from '../../../widget_library/jsonquery/jsonquery.js';

export class ObjectFormatter {

    static objectfunctions = {
        compose: (data, object) => ObjectFormatter.compose(data, object),
        concat: (value, stringTemplate) => ObjectFormatter.concat(value, stringTemplate)
    }


    static compose(data: any, object: Object): any {
        let newObj = object;
        for (let o in object) {
            newObj[o] = jsonQuery(object[o], { data: data }).value
        }
        return newObj;
    }


    static concat(value: any, stringTemplate: any): any {

        // get build function
        let func = this.buildFunction(value, stringTemplate);

        let params = [];
        for (var v in value) {
            params.push(value[v]);
        }
        return func(...params);
    }

    static buildFunction = (value, s) => {

        let params = [];
        for (var v in value) {
            params.push(v);
        }

        params.push(`return \`${s}\``)
        return new Function(...params);
    }
}