import { LitElement } from "lit-element";
import { Guid } from "../guid";
import { MakeIm } from "../MakeImmutable";



export interface PropertyChangedObserverDeclaration {
    property: string;
    callback: { (oldValue: any, newValue: any): void }
}

export interface IPropertyChangedObserver {
    invoke: { (property: string, oldValue: any, newValue: any): void }
};


export interface VisibilityChangedObserver { (visible: boolean): void };
export interface RemoveVisibilityChangedObserver { (): void };


/*
 * LitElement extensions
 */
declare module 'lit-element' {
    interface LitElement {
        raiseEvent<T>(name: string, value: T): void;
        cancelEventPropagation(e: Event): void;
        getShadowRoot(): ShadowRoot;
        addVisibilityChangedObserver(element: Element, visibilityChangedCallback: VisibilityChangedObserver);
        addPropertyChangedObserver(...args: PropertyChangedObserverDeclaration[]): IPropertyChangedObserver;
    }
}

if (!LitElement.prototype.raiseEvent) {
    LitElement.prototype.raiseEvent = function <T>(name: string, value: T) : void {
        this.dispatchEvent(new CustomEvent(name, {
            detail: MakeIm(value),
            bubbles: true,
            cancelable: true,
            composed: true
        }));        
    }
}

if (!LitElement.prototype.cancelEventPropagation) {
    LitElement.prototype.cancelEventPropagation = function (e: Event): void {
        e.cancelBubble = true;
        e.stopImmediatePropagation();
        e.stopPropagation();
    }
}

if (!LitElement.prototype.getShadowRoot) {
    LitElement.prototype.getShadowRoot = function (): ShadowRoot {
        return (this.getRootNode() as any).host.shadowRoot as ShadowRoot;
    }
}

if (!LitElement.prototype.addVisibilityChangedObserver) {
    LitElement.prototype.addVisibilityChangedObserver = function (element: Element, visibilityChangedCallback: VisibilityChangedObserver): RemoveVisibilityChangedObserver {
        let observer: IntersectionObserver;
        observer = new IntersectionObserver(io => {
            if (io?.length > 0) {
                visibilityChangedCallback(io[0].isIntersecting);
            }
        });
        observer.observe(element);
        let id = `_${Guid.newGuid().toString()}`;
        this[id] = observer;
        return () => {
            observer.disconnect();
            this[id] = null;
        }
    }
}

if (!LitElement.prototype.addPropertyChangedObserver) {
    /**
     * To use this feature override the method attributeChangedCallback(name, oldVal, newVal) in your 
     * derived class and call the PropertyChangedObserver method invoke (return value from this method).
     * 
     */
    LitElement.prototype.addPropertyChangedObserver = function (...args: PropertyChangedObserverDeclaration[]): IPropertyChangedObserver {
        let observers = {};

        for (let arg of args) {
            observers[arg.property.toLowerCase()] =  arg.callback;
        }

        return <IPropertyChangedObserver>{
            invoke: (property: string, oldValue: string, newValue: string) => {
                if (observers[property]) {
                    observers[property](oldValue, newValue);
                }
            }
        }
    }
}

