import { html, TemplateResult } from "lit-html";
import { DataRetriever } from "../../helpers/dataretriever";
import { DataValidator } from "../../helpers/datavalidator";
import { Field } from "../../models/templatemodel";


export class CheckboxService {
    private _dataHelper: DataRetriever;

    public constructor(dataHelper: DataRetriever) {
        this._dataHelper = dataHelper;
    }

    public getCheckboxWithLabel(data: any, field: Field): TemplateResult {
        let label = field.Label;
        let temp = this._dataHelper.getDataFromField(data, field);
        let isEmpty = DataValidator.isEmpty(temp);
        if (isEmpty) {
            temp = "false";
        }
        temp = temp.toLowerCase();
        let checkbox = (temp === "true" || temp === "yes") ? html`<input type='checkbox' checked />` : html`<input type='checkbox' />`;

        let text = html`<div class="checkboxContainer">
                        ${checkbox}
                         <span class="checkmark"></span>
                        <span class="${field.Format}">${label}</span>
                    </div>
                    `;

        return this._dataHelper.outputValidator(data, field, text);
    }
}
