import { html, TemplateResult } from "lit-html";
import { Field } from "../../models/templatemodel";

export class LinkService {

    static getLinktoHTML(data: any, field: Field): TemplateResult {
        return html`<span class="label">${field.Label} </span>
                    <a class="${field.Format}" href="url" target="_blank">${data[field.Value]}</a>`;
    }
}