import { html, TemplateResult } from "lit-html";
import { DateFormatter } from "../../shared/dateformat";
import { Field } from "../models/templatemodel";
import { DataFormatter } from "./dataformatter";
import { DataValidator } from "./datavalidator";

export class DataRetriever {

    public _dateFormatter: DateFormatter = null;

    public constructor() {
        this._dateFormatter = new DateFormatter();
    }

    public getDataFromField(data: any, field: Field): any {
        return this.formatStringValues(this.getStringFromObject(data, field.Value));
    }
    public getStringFromObject(data: any, field: any): any {

        if (field.includes(".")) {

            let [object, value] = this.getSplitValues(field, ".")
            let dataObject = data[object];

            if (Array.isArray(dataObject)) {
                return this.getValueFromKey(dataObject, value);
            } else {
                return dataObject[value];
            }
        }
        return data[field];
    }

    private formatStringValues(value: any): any {
        if (value == "true") {
            return "Yes";
        } else if (value == "false") {
            return "No";
        }
        return value;
    }

    public getConcattedValuesWithSeparator(data: any, field: Field): any {

        let array = field.Value.split("|");
        let element;
        let text = '';
        for (let i = 0; i < array.length; i++) {
            element = array[i];
            element = this.getStringFromObject(data, element);
            text += element;
            if (i != array.length - 1) { text += " " + field.Format + " "; }
        }
        return text;
    }



    public toggleLabel(field: Field): TemplateResult {
        let hideLabel = DataValidator.isEmpty(field.Label);
        return html`${hideLabel ? html`` : html`<span class="label">${field.Label.replace("!", "")}&nbsp;</span>`}`;
    }

    public toggleValue(field: Field): TemplateResult {
        let hideValue = DataValidator.isEmpty(field.Value);
        return html`${hideValue ? html`` : html`<span class="text ${field.Format}">${field.Value}&nbsp;</span>`}`;
    }


    public isEmptyValue(data: any, label: any): boolean {
        let missingValue = data === null || data === "" || data === undefined;
        return (missingValue && label.includes("!")) ? true : false;
    }

    public outputValidator(data: any, field: Field, input: TemplateResult): TemplateResult {
        try {
            let tempData = this.getReturnType(data, field);
            return this.isEmptyValue(tempData, field.Label) ? html`` : DataFormatter.encapsulateFieldTypeOutputInSpan(input, field);
        } catch (error) {
            console.log(`Error in field type ${field.Type}, check that label: %c${field.Label}%c and value: %c${field.Value}%c are correct.`, "color: red", "color: black", "color: red", error);
            console.log(error.message);
        }
    }

    public getSplitValues(data: any, seperator: string): any {
        let objectToSplit = data.split(seperator);
        return [objectToSplit[0], objectToSplit[1]];
    }


    public getLabelWithLabelFormatting(field: Field): TemplateResult {
        let label = this.toggleLabel(field);
        return DataFormatter.encapsulateTextInSpanWithClass(label, "label");
    }


    public getValueFromKey(dataObject: any, key: any): any {

        for (let i = 0; i < dataObject.length; i++) {
            let element = dataObject[i];

            if (element["Key"] == key) {
                return element["Value"];
            }
        }
    }
    public getElementFromKey(dataObject: any, key: any, value: any): any {

        let element;
        for (let i = 0; i < dataObject.length; i++) {
            element = dataObject[i];
            if (element[key] == value) {
                return element;
            }
        }
    }


    public formatNumbers(data: any, format: any): any {

        let number = data;
        if (format != null && /^\d+$/.test(format)) {
            let roundedNumber = Math.pow(10, parseFloat(format));
            number = Math.round(data * roundedNumber) / roundedNumber;
        }
        return number;
    }


    public getReturnType(data: any, field: Field): any {
        if (data instanceof Date) {
            // is date 
            return this._dateFormatter.dateFormat(new Date(data), field.Format);
        }
        else if (/^[0-9,.]*$/.test(data)) {
            // is number
            let result;
            if (field.Format.includes(".")) {
                let formatArray = field.Format.split(".");
                result = this.formatNumbers(data, formatArray[0]) + " " + formatArray[1];
            }
            else {
                result = this.formatNumbers(data, field.Format)
            }
            return result;
        }
        else {
            // is string
            return this.getDataFromField(data, field);
        }
    }
}