import { html, TemplateResult } from "lit-html";
import { DataRetriever } from "../../helpers/dataretriever";
import { Field } from "../../models/templatemodel";

export class IconService {
    private _dataHelper: DataRetriever;

    public constructor(dataHelper: DataRetriever) {
        this._dataHelper = dataHelper;
    }

    public getIconWithLabel(data: any, field: Field): TemplateResult {
        let label = field.Label;
        let icon = field.Value;

        let text = html`<img src=${icon} alt="${label}">
                    <span class="${field.Format}">${label}</span>`;

        return this._dataHelper.outputValidator(data, field, text);
    }

    public getLineSeperator(data: any, field: Field): TemplateResult {
        return html`<span id="${field.Id}" class="FieldType_${field.Type}"><div class="border"></div></span>`;
    }

    public getEmptyLine(data: any, field: Field): TemplateResult {
        return html`<span id="${field.Id}" class="FieldType_${field.Type}">Placeholder</span>`;
    }
}