import { customElement, html, LitElement, property, TemplateResult } from "lit-element";
import { TemplateRetriever } from "../../genericdetailviewmanager/services/templateretriever";
import { cancelclick } from "../../shared/@cancelclick";
import "../../shared/extentions/lit-element.extensions";
import { TemplateEngine } from "../engine/templateengine";

@customElement("generic-detail-view")
class GenericDetailView extends LitElement {
    private templateEngine: TemplateEngine = null;

    public constructor() {
        super();
        let templateRetriever = new TemplateRetriever();
        this.templateEngine = new TemplateEngine(templateRetriever, this);
    }

    //-------------------------------------------------------------------------
    // 
    //  Private properties Section...
    //
    //-------------------------------------------------------------------------
    @property() data: any;
    @property() template: any;
    @property({ type: String }) viewClass: string;
    @property({ type: String }) stylingPath: string;
    @property() loadExternalData: any;
    @property() templateId: any;
    @property() openPopUpExternalData: any

    @cancelclick() public firstUpdated(): void { }

    public reRender(data, template): void {
        this.data = data;
        this.template = template;
        this.requestUpdate();

    }

    //-------------------------------------------------------------------------
    // 
    //  Private methods Section...
    //
    //-------------------------------------------------------------------------

    protected render(): TemplateResult {
        if (!this.data) {
            this.requestUpdate();
            return null;
        }
        return html`
        <link rel='stylesheet' href='/assets/css/genericdetailview.css'>
        <link rel='stylesheet' href='${this.stylingPath}'>
        <link rel="stylesheet" href="/api/css/?tag=${this.tagName}&cid=${this.templateId}">
                <div class="panel">
                    <div class='detail-view ${this.viewClass}'">
                        <div class='content'>
                            ${this.templateEngine.renderData(this.data, this.template)}
                        </div>
                </div>
            </div> `;
    }
}