import { html, TemplateResult } from "lit-html";
import { Field } from "../models/templatemodel";

export class DataFormatter {

    static getTextSpanWithFormatting(data: any, field: Field): TemplateResult {
        return html` <span class="text ${field.Format}" > ${data} </span>`;
    }

    static encapsulateFieldTypeOutputInSpan(data: any, field: Field): TemplateResult {
        return html`<span id="${field.Id}" class="FieldType_${field.Type}">${data}</span>`;
    }

    static encapsulateTextInSpanWithClass(data: any, className: any): TemplateResult {
        return html`<span class="${className}">${data}</span>`;
    }
}