import { Section } from "../../models/templatemodel";

export class ListService {


    static getSortedList(list: any, section: Section): any {

        let sortorder = section.Options.SortOrder;
        if (section.Options.SortOrder == undefined) {
            return list;
        }
        let sortValue = section.Fields[0].Value;

        switch (sortorder) {
            case "Asc": {
                return this.sortList(list, sortValue, 1);
            }
            case "Desc": {
                return this.sortList(list, sortValue, -1);
            }
            default: {
                return list;
            }
        }
    }

    static sortList(list: any, sortValue: any, number: number): any {
        return list.sort((a, b) => number * (a[sortValue].localeCompare(b[sortValue])));
    }
}