import { html, TemplateResult } from "lit-html";
import { DataFormatter } from "../../helpers/dataformatter";
import { DataRetriever } from "../../helpers/dataretriever";
import { DataValidator } from "../../helpers/datavalidator";
import { Field } from "../../models/templatemodel";

export class TextService {
    private _dataHelper: DataRetriever;

    public constructor(dataHelper: DataRetriever) {
        this._dataHelper = dataHelper;
    }

    public getTextOneLine(data: any, field: Field): TemplateResult {
        let tempData = this._dataHelper.getDataFromField(data, field);
        let dataSpan = DataFormatter.encapsulateTextInSpanWithClass(tempData, "text " + field.Format);
        let text = html`${this._dataHelper.toggleLabel(field)} ${dataSpan}`;
        return this._dataHelper.outputValidator(data, field, text);
    }

    public getTextTwoLines(data: any, field: Field): TemplateResult {
        let tempData = this._dataHelper.getDataFromField(data, field);
        let labelSpan = this._dataHelper.toggleLabel(field);

        if (DataValidator.isEmpty(tempData)) {
            try {
                return (field.Label.includes("!")) ? html`` : html`${labelSpan}`;
            } catch (error) {
                console.log("Field.Label needs a value.");
                return html``;
            }

        };
        let isMultiline = tempData.length > 60;
        let textClass = isMultiline ? "multiline" : "";
        let formatting = DataValidator.getValidFormat(field.Format);
        let text;
        let isNumber = DataValidator.isNumber(formatting);
        let isEmpty = DataValidator.isEmpty(formatting);

        let dataSpan;
        if (isMultiline && (isNumber || isEmpty)) {
            let styling = isNumber && !isEmpty ? formatting : 5;

            dataSpan = DataFormatter.encapsulateTextInSpanWithClass(tempData, "text")
            text = html`${labelSpan} <div class="textContainer ${textClass}" style = "max-height:${styling}em" id = "${textClass}" > ${dataSpan}</div> `;
        }
        else {
            dataSpan = DataFormatter.encapsulateTextInSpanWithClass(tempData, "text " + formatting);
            text = html`${labelSpan}${dataSpan}`;
        }

        return this._dataHelper.outputValidator(data, field, text);
    }

    public getFieldValueToString(data: any, field: Field): string {
        return field.Value;
    }

    public getDataValueToString(data: any, field: Field): string {
        return data[field.Value];
    }

    public getTextNoLabel(data: any, field: Field): TemplateResult {
        try {
            let tempData = this._dataHelper.getDataFromField(data, field)
            let text = DataFormatter.getTextSpanWithFormatting(tempData, field);
            return DataFormatter.encapsulateFieldTypeOutputInSpan(text, field);
        } catch (error) {
            console.log("Error in text with no label properties");
        }
    }

    public getArrayValues(data: any, field: Field): TemplateResult {
        let array = data[field.Value];
        let labelWithFormatting = this._dataHelper.getLabelWithLabelFormatting(field);
        if (array.length > 0) {
            let text = html` 
                 ${labelWithFormatting} <span class="arrayContainer">${array.map(i => html` <span>${i}</span> `)}</span>`;
            return DataFormatter.encapsulateFieldTypeOutputInSpan(text, field);
        }
        let text = html`${labelWithFormatting}`;
        return DataFormatter.encapsulateFieldTypeOutputInSpan(text, field);
    }

    public getValueWithDenomination(data: any, field: Field): TemplateResult {

        let tempData = this._dataHelper.getDataFromField(data, field);
        let dataSpan = DataFormatter.encapsulateTextInSpanWithClass(tempData, field.Format);
        let labelSpan = this._dataHelper.toggleLabel(field);
        let text = html`${labelSpan} ${dataSpan}`;
        return this._dataHelper.outputValidator(data, field, text);
    }

    public staticText(data: any, field: Field): TemplateResult {
        let text = html`${this._dataHelper.toggleLabel(field)}${this._dataHelper.toggleValue(field)}`;
        return DataFormatter.encapsulateFieldTypeOutputInSpan(text, field);
    }

    public formattedNumbers(data: any, field: Field): TemplateResult {
        let formatArray = field.Format.split(".");
        let textNumber = parseFloat(this._dataHelper.getDataFromField(data, field));
        let roundedNumber = Math.pow(10, parseFloat(formatArray[0]));
        let formattedText = Math.round(textNumber * roundedNumber) / roundedNumber;
        let text = html`${this._dataHelper.toggleLabel(field)}
                <span class="text">${formattedText} ${formatArray[1]}</span>`;

        return this._dataHelper.outputValidator(data, field, text);
    }

    public getSplitStringOnFirstOccurenceSelectSpecificPart(data: any, field: Field, item: any): TemplateResult {
        try {
            let separator = field.Format;
            let tempData = (DataValidator.isEmpty(this._dataHelper.getDataFromField(data, field))) ? "" : this._dataHelper.getDataFromField(data, field);
            let text = tempData.split(separator)[item];
            let output = html`${this._dataHelper.toggleLabel(field)} ${DataFormatter.encapsulateTextInSpanWithClass(text, "text")}`;
            return (DataValidator.isEmpty(this._dataHelper.getDataFromField(data, field)) && field.Label.includes("!")) ? html`` : DataFormatter.encapsulateFieldTypeOutputInSpan(output, field);
        } catch (error) {
            return html``;
        }

    }

    public customBooleanValues(data: any, field: Field): TemplateResult {
        let booleanValues = field.Format.split(".");
        let decider = this._dataHelper.getDataFromField(data, field).toLowerCase() === "true" || this._dataHelper.getDataFromField(data, field).toLowerCase() === "yes"
        let text = html`${this._dataHelper.toggleLabel(field)}
                <span class="text">${decider ? html`${booleanValues[0]}` : html`${booleanValues[1]}`}</span>`;

        return this._dataHelper.outputValidator(data, field, text);
    }

    public getConcattedValues(data: any, field: Field): TemplateResult {
        let text = this._dataHelper.getConcattedValuesWithSeparator(data, field)
        let input = html`${this._dataHelper.toggleLabel(field)}${DataFormatter.encapsulateTextInSpanWithClass(text, "text")}`;
        return DataFormatter.encapsulateFieldTypeOutputInSpan(input, field);
    }

    public getStringReplaceWithData(data: any, field: Field): TemplateResult {
        try {
            let array = field.Value.split("|");
            let tempData;
            let stringToBeUpdated = array[0];
            for (let i = 1; i < array.length; i++) {
                let tempField = array[i];
                tempData = this._dataHelper.getStringFromObject(data, tempField)
                stringToBeUpdated = stringToBeUpdated.replace("$", tempData);
            }
            let text = html`${this._dataHelper.toggleLabel(field)}${DataFormatter.encapsulateTextInSpanWithClass(stringToBeUpdated, "text")}`;
            return DataFormatter.encapsulateFieldTypeOutputInSpan(text, field);
        } catch (error) {
            console.log(error);
        }
    }


    public getValueInComplexObject(data: any, field: Field): TemplateResult {
        let objectToSplit = field.Value.split(".");

        let temp;
        let result;
        let element;
        for (let i = 0; i < objectToSplit.length; i++) {
            temp = objectToSplit[i];
            if (temp.includes("[")) {
                let [object, rest] = this._dataHelper.getSplitValues(temp, "[")

                if (rest.includes("|")) {
                    let [key, value] = this._dataHelper.getSplitValues(rest, "|")
                    let val = value.split("]")[0];
                    let objectData = data[object];
                    element = this._dataHelper.getElementFromKey(objectData, key, val);
                    result = element;
                    continue;
                } else {
                    let elm = element[object];
                    let tempVal = rest.split("]")[0];
                    result = elm[tempVal];
                    continue;
                }
            } else {
                result = this._dataHelper.getReturnType(result[temp], field);
                continue;
            }
        }

        result = html`${this._dataHelper.toggleLabel(field)} ${DataFormatter.encapsulateTextInSpanWithClass(result, "textcontent " + field.Format)}`;
        return DataFormatter.encapsulateFieldTypeOutputInSpan(result, field);
    }
}