export class BooleanFormatter {

    static booleanfunctions = {
        customBooleanValue: (value, trueValue, falseValue) => BooleanFormatter.setCustomBooleanValue(value, trueValue, falseValue),
        join: (array, separator) => BooleanFormatter.join(array, separator),

    }

    static setCustomBooleanValue(value: any, trueValue: string, falseValue: string): string {

        if (value instanceof Array) {
            value.map(e => e == 'true' ? trueValue : falseValue);
        }
        return value === 'true' ? trueValue : falseValue
    }

    static join(array: any, separator: string): string {
        return Array.prototype.join.apply(array, separator);
    }

}