import { Field } from "../models/templatemodel";

export class DataValidator {


    static isEmpty(value: any): boolean {
        return value === null || value === "" || value === undefined;
    }

    static isEmptyDataWithLabel(data: any, label: any): boolean {
        let missingValue = this.isEmpty(data);
        return (missingValue && label.includes("!")) ? true : false;
    }

    static isValidData(data: any, field: Field): boolean {
        try {
            return this.isEmptyDataWithLabel(data, field.Label);
        } catch (error) {
            console.log(`Error in field type ${field.Type}, check that label: %c${field.Label}%c and value: %c${field.Value}%c are correct.`, "color: red", "color: black", "color: red", error);
            console.log(error.message);
        }
    }

    static isNumber(number: any): boolean {
        return /^[0-9,.]*$/.test(number)
    }

    static isValidFormat(format: any): boolean {
        return !this.isEmpty(format);
    }

    static getValidFormat(format: any): string {
        let tempFormat = this.isValidFormat(format) ? format : '';
        return tempFormat;
    }
}