export function isGuidNullOrEmpty(guid: string): boolean {
    if (!guid || !guid[0]) return true;
    if (guid == "00000000-0000-0000-0000-000000000000") return true;
    return false;
}


export class Guid {

    private static _empty : Guid = null;

    private _id: string;

    public constructor();
    public constructor(guid: string);
    public constructor(...params: any[]) {
        switch (params.length) {
            case 0: this._id = this.createId(); break;
            case 1: this._id = this.verifyOrDie(params[0] as string); break;
            default: 
        }
    }

    public toString(): string {
        return this._id;
    }

    private createId(): string {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
            var r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    private verifyOrDie(value: string): string {
        if (value.match(/^[\da-f]{8}-[\da-f]{4}-[\da-f]{4}-[\da-f]{4}-[\da-f]{12}$/i)) {
            return value.toUpperCase();
        }
        throw `${value} is not a legal Guid`;
    }

    public static newGuid() : Guid {
        return new Guid();
    }

    public static get empty(): Guid {
        if (!Guid._empty) {
            Guid._empty = new Guid('00000000-0000-0000-0000-000000000000');
        }
        return Guid._empty; 
    }
}